import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

function NavBasic() {
  return (
    <Navbar style={{
      backgroundColor: "rgb(6, 6, 69)",
    }} data-bs-theme="dark">
      {/* bg="dark" data-bs-theme="dark" */}
        {/* expand="lg" className="bg-body-tertiary" */}
    <Navbar ></Navbar>
      <Container>
        <Navbar.Brand href="#home">Haz Azul<br/>Tecnologa e Innovacin</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#home">Nosotros</Nav.Link>
            <Nav.Link href="#link">Links</Nav.Link>
            <NavDropdown title="NEGOCIO" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                Another action
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">
                Separated link
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavBasic;